* {
  font-family: Lato, serif;
}

.button-active {
  cursor: pointer;
}

.button-active:hover {
  background-color: #f13d7f;
}

.button-active:active {
  position: relative;
  top: 0.5px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.2);
  background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.4));
}

.button-pressed {
  cursor: pointer;
}

.button-pressed:hover {
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.3);
}

.button-pressed:active {
  position: relative;
  top: 0.5px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(to bottom, transparent, rgba(0, 0, 0, 0.1));
}

.button-pressed-wtheff {
  cursor: pointer;
}

.button-pressed-wtheff:active {
  position: relative;
  top: 0.5px;
}

.col2 {
  width: calc(16.66667% - 30px);
}

.col5 {
  width: calc(41.66667% - 30px);
}

.col3 {
  width: calc(25% - 30px);
}

.col4 {
  width: calc(33.33333% - 30px);
}

.col1 {
  width: calc(8.33333% - 30px);
}

.margin-top {
  margin-top: 50px;
}

.even,
.odd {
  color: #222222;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.sprite {
  background: url(../img/spritesheet.png) no-repeat;
  display: block;
}

.sprite-Forma-1 {
  width: 32px;
  height: 29px;
  background-position: -5px -5px;
}

.sprite-Forma-1-_1_ {
  width: 40px;
  height: 40px;
  background-position: -5px -44px;
}

.sprite-Forma-1-_2_ {
  width: 47px;
  height: 35px;
  background-position: -5px -94px;
}

.sprite-Forma-1-_3_ {
  width: 45px;
  height: 32px;
  background-position: -5px -139px;
}

.sprite-Forma-1-copy {
  width: 23px;
  height: 22px;
  background-position: -5px -181px;
}

.header {
  height: 101px;
  border-bottom: 1px solid #ececec;
  display: flex;
  align-items: center;
}

.header__block {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  width: 1140px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.leftblock {
  display: flex;
  align-items: center;
}

.header__logo {
  float: left;
  margin-right: 30px;
}

.header__search {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.header__search-form {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.header__search-categorybutton {
  position: relative;
  display: flex;
  border-bottom-left-radius: 3px;
  border-top-left-radius: 3px;
  border: 1px solid #e6e6e6;
  box-sizing: border-box;
  background: -webkit-linear-gradient(-90deg, #f9f9f9 0%, #f5f5f5 100%);
  background: -moz-linear-gradient(-90deg, #f9f9f9 0%, #f5f5f5 100%);
  background: -o-linear-gradient(-90deg, #f9f9f9 0%, #f5f5f5 100%);
  background: -ms-linear-gradient(-90deg, #f9f9f9 0%, #f5f5f5 100%);
  background: linear-gradient(180deg, #f9f9f9 0%, #f5f5f5 100%);
  width: 87px;
  height: 38px;
  text-align: center;
  vertical-align: middle;
  color: #222222;
  font-size: 14px;
  font-weight: 300;
  line-height: 32px;
  justify-content: space-around;
  align-items: center;
}

.header__search-check {
  display: none;
}

.submenu,
.submenu2 {
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0;
  transition: .5s ease-in-out;
  position: absolute;
  top: 38px;
  left: 0;
  z-index: 2;
  border: 1px solid #e9e9e9;
  border-radius: 10px;
  background-color: white;
  width: 260px;
  padding: 15px;
  text-align: left;
}

.submenu h1,
.submenu2 h1 {
  color: #232323;
  font-size: 14px;
  font-weight: 400;
  line-height: 32px;
  text-transform: uppercase;
}

.submenu a,
.submenu2 a {
  color: #4e4e4e;
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  text-decoration: none;
}

.submenu a:hover,
.submenu2 a:hover {
  text-shadow: 1px 1px #732372;
}

.submenu2 {
  width: 220px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.checkout,
.gotocart {
  border: 1px solid #f16d7f;
  color: #f16d7f;
  text-transform: uppercase;
  height: 50px;
  line-height: 50px;
  text-align: center;
  margin-bottom: 10px;
}

.gotocart {
  color: #eaeaea;
  border: 1px solid #eaeaea;
}

#check_category:checked + .submenu,
#check_category:checked + .submenu2 {
  opacity: 1;
  transform: scaleY(1);
}

.header__account-basket {
  position: relative;
}

#check_basket:checked + .submenu2 {
  opacity: 1;
  transform: scaleY(1);
}

.header__search-categorybutton-droparrow {
  color: #838383;
  font-size: 14px;
  font-weight: 400;
  line-height: 32px;
}

.header__search-input {
  border: 1px solid  #e6e6e6;
  box-sizing: border-box;
  background-color: #ffffff;
  width: 280px;
  height: 38px;
  padding: 15px;
}

.header__search-input::placeholder {
  color: #a4a4a4;
  font-size: 14px;
  font-weight: 300;
  line-height: 32px;
}

.header__search-searchbutton {
  display: flex;
  border-bottom-right-radius: 3px;
  border-top-right-radius: 3px;
  border: 1px solid #e6e6e6;
  box-sizing: border-box;
  background-color: #ffffff;
  width: 37px;
  height: 38px;
  justify-content: space-around;
  align-items: center;
}

.header__searchbutton-searchicon {
  color: #a4a4a4;
  font-size: 14px;
  font-weight: 400;
  line-height: 32px;
}

.header__account {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header__account-button {
  background-color: #f16d7f;
  width: 130px;
  height: 38px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #ffffff;
  font-size: 15px;
  font-weight: 400;
  line-height: 32px;
  margin-left: 25px;
}

.header__account-button-droparrow {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  line-height: 32px;
}

.wrapper {
  max-width: 1140px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.header__menu {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.header__menu-list {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(66.66667% - 30px);
  display: flex;
  justify-content: space-around;
}

.header__menu-item {
  display: block;
  height: 50px;
  min-width: 50px;
  text-align: center;
}

.header__menu-item-link {
  color: #222222;
  font-size: 14px;
  font-weight: 700;
  line-height: 50px;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
  width: 100%;
  height: 100%;
}

.header__menu-item-link:hover {
  text-shadow: 1px 1px #732372;
  transition: text-shadow 1s ease-out 0s;
}

@media screen and (max-width: 960px) {
  .header__block {
    padding-left: 20px;
    padding-right: 20px;
  }

  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 780px) {
  .header__block {
    padding-left: 15px;
    padding-right: 15px;
  }

  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .header__block {
    padding-left: 10px;
    padding-right: 10px;
  }

  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 420px) {
  .header__block {
    padding-left: 10px;
    padding-right: 10px;
  }

  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}